<template>
  <div class="partner-company">
    <el-container>
      <el-header class="header">
        <!-- 搜索表单 start -->
        <el-form
          :inline="true"
          :model="searchValidateForm"
          ref="searchValidateForm"
          label-width="100px"
          class="search-validate-form"
          :rules="searchValidateFormRules"
        >
          <!-- 请选择景区名称 start -->
          <el-form-item label="" prop="spotId">
            <el-select
              style="width: 100%;"
              v-model="searchValidateForm.spotId"
              filterable
              placeholder="请选择景区名称"
              clearable
            >
              <el-option
                v-for="(item, index) in searchScenicOptions"
                :key="index"
                :label="item.SCENIC_SPOT_NAME"
                :value="item.SCENIC_SPOT_ID"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 请选择景区名称 end -->

          <!-- 请输入申请单号 start -->
          <el-form-item label="" prop="applyNumber">
            <el-input
              v-model="searchValidateForm.applyNumber"
              placeholder="请输入申请单号"
            >
            </el-input>
          </el-form-item>
          <!-- 请输入申请单号 end -->

          <!-- 请选择配件名称 start -->
          <el-form-item label="" prop="partsManagementName">
            <el-select
              v-model="searchValidateForm.partsManagementName"
              filterable
              placeholder="请选择配件名称"
              clearable
            >
              <el-option
                v-for="(item, index) in searchGoodsNameOptions"
                :key="index"
                :label="item.name"
                :value="item.id"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 请选择配件名称 end -->

          <!-- 请输入设备编号 start -->
          <el-form-item label="" prop="equipmentNo">
            <el-input
              v-model="searchValidateForm.equipmentNo"
              placeholder="请输入设备编号"
            >
            </el-input>
          </el-form-item>
          <!-- 请输入设备编号 end -->

          <!-- 请选择类型 start -->
          <el-form-item label="" prop="type">
            <el-select
              v-model="searchValidateForm.type"
              filterable
              placeholder="请选择类型"
              @change="onSearchChangeType"
            >
              <el-option
                v-for="(item, index) in searchTypeOptions"
                :key="index"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 请选择类型 end -->
        </el-form>
        <!-- 搜索表单 end -->

        <!-- 查询数据 start -->
        <el-button
          type="primary"
          icon="el-icon-search"
          @click="onSearch('searchValidateForm')"
          >搜索</el-button
        >
        <!-- 查询数据 end -->

        <!-- 重置 start -->
        <el-button type="primary" icon="el-icon-refresh-right" @click="onReset"
          >重置</el-button
        >
        <!-- 重置 end -->

        <!-- 新增 start -->
        <el-button
          type="primary"
          icon="el-icon-circle-plus-outline"
          @click="onAdd"
          >新增</el-button
        >
        <!-- 新增 end -->

        <!-- 导出 Excel start -->
        <el-button
          type="primary"
          :loading="downloadLoading"
          icon="el-icon-download"
          @click="exportExcel"
          >导出Excel</el-button
        >

        <!-- 隐藏的a标签，用来导出excel -->
        <a id="exportA"  :href="downloadUrl" download> </a>
        <!-- 导出 Excel end -->

        <!-- 导入EXCEL start -->
        <el-upload
          ref="uploadImport"
          action="https://run.mocky.io/v3/9d059bf9-4660-45f2-925d-ce80ad6c4d15"
          :limit="1"
          :on-exceed="handleImportExceed"
          :on-change="handleImportChange"
          :show-file-list="false"
          accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
          :auto-upload="false"
          flex
          justify-end
        >
          <el-button
            type="primary"
            :loading="importLoading"
            icon="el-icon-upload2"
            >导入EXCEL</el-button
          >
          <!-- <template #tip>
            <div class="el-upload__tip">
              jpg/png files with a size less than 2M.
            </div>
          </template> -->
        </el-upload>
        <!-- 导入EXCEL end -->
      </el-header>

      <el-main>
        <!-- 表格 start -->
        <el-table
          class="table"
          :data="tableData"
          style="width: 100%"
          border
          v-loading="tableLoading"
          :header-cell-style="{
            background: '#f2f2f2',
            color: '#606266',
            height: '38px',
            padding: '0px'
          }"
          :cell-style="{ height: '38px', padding: '0' }"
        >
          <el-table-column
            v-for="(value, key) in tableFields"
            :key="key"
            :prop="value"
            :label="key"
            min-width="130"
          >
            <template slot-scope="scope">
              <div>
                {{ tableContentValue(scope.row, value) }}
              </div>
            </template>
          </el-table-column>
          <el-table-column label="操作" min-width="130">
            <template slot-scope="scope">
              <el-button
                @click.native.prevent="editRow(scope.$index, scope.row)"
                type="text"
                size="small"
              >
                编辑
              </el-button>
            </template>
          </el-table-column>
        </el-table>
        <!-- 表格 end -->

        <!-- 导出的表格 start -->
        <!-- 此表格始终保持隐藏 -->
        <el-table
          id="outTable"
          class="table"
          :data="exportTableData"
          style="width: 100%; display: none"
          border
        >
          <el-table-column
            v-for="(value, key) in tableFields"
            :key="key"
            :prop="value"
            :label="key"
            min-width="130"
            align="center"
          >
          </el-table-column>
        </el-table>
        <!-- 导出的表格 end -->
      </el-main>

      <el-footer>
        <!-- 分页 start -->
        <div class="block">
          <el-pagination
            background
            layout="prev, pager, next, total, sizes, jumper"
            :total="total"
            :current-page="currentPage"
            :page-sizes="[10, 20, 30, 40]"
            :page-size="pageSize"
            @current-change="onCurrentChange"
            @size-change="onSizeChange"
          >
          </el-pagination>
        </div>
        <!-- 分页 end -->
      </el-footer>

      <!-- 提交 dialog 对话框 start -->
      <el-dialog
        class="dialog"
        :title="dialogTitle"
        :visible.sync="dialogVisible"
        :before-close="handleClose"
      >
        <!-- form表单 -->
        <el-form
          :model="validateForm"
          ref="validateForm"
          label-width="120px"
          class="demo-ruleForm"
          :rules="validateFormRules"
        >
          <!-- 景区名称 start -->
          <el-form-item label="景区名称" prop="spotId">
            <el-select
              style="width: 100%;"
              v-model="validateForm.spotId"
              filterable
              placeholder="景区名称"
              clearable
            >
              <el-option
                v-for="(item, index) in scenicOptions"
                :key="index"
                :label="item.SCENIC_SPOT_NAME"
                :value="item.SCENIC_SPOT_ID"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 景区名称 end -->

          <!-- 申请单号 start -->
          <el-form-item label="申请单号" prop="applyNumber">
            <el-input
              type="text"
              v-model="validateForm.applyNumber"
              placeholder="申请单号"
            ></el-input>
          </el-form-item>
          <!-- 申请单号 end -->

          <!-- 请选择配件名称 start -->
          <el-form-item label="配件名称" prop="partsManagementName">
            <el-select
              style="width: 100%;"
              v-model="validateForm.partsManagementName"
              filterable
              placeholder="请选择配件名称"
              clearable
              @change="onChangeGoods"
            >
              <el-option
                v-for="(item, index) in searchGoodsNameOptions"
                :key="index"
                :label="item.name"
                :value="item.name"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 请选择配件名称 end -->

          <!-- 配件编号 start -->
          <el-form-item label="配件编号" prop="partsManagementId">
            <el-input
              type="text"
              v-model="validateForm.partsManagementId"
              placeholder="配件编号"
              disabled
            ></el-input>
          </el-form-item>
          <!-- 配件编号 end -->

          <!-- 规格型号 start -->
          <el-form-item label="规格型号" prop="accessoryModel">
            <el-input
              type="text"
              v-model="validateForm.accessoryModel"
              placeholder="规格型号"
              disabled
            ></el-input>
          </el-form-item>
          <!-- 规格型号 end -->

          <!-- 类型 start -->
          <el-form-item label="类型" prop="type">
            <el-select
              style="width: 100%;"
              v-model="validateForm.type"
              filterable
              placeholder="请选择类型"
            >
              <el-option
                v-for="(item, index) in typeOptions"
                :key="index"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 类型 end -->

          <!-- 入库数量 start -->
          <el-form-item
            label="入库数量"
            prop="number"
            v-if="validateForm.type == '2'"
          >
            <el-input
              type="text"
              v-model="validateForm.number"
              placeholder="入库数量"
            ></el-input>
          </el-form-item>
          <!-- 入库数量 end -->

          <!-- 入库原因 start -->
          <el-form-item
            label="入库原因"
            prop="reason"
            v-if="validateForm.type == '2'"
          >
            <el-select
              style="width: 100%;"
              v-model="validateForm.reason"
              filterable
              placeholder="请选择入库原因"
              clearable
            >
              <el-option
                v-for="(item, index) in searchInStockReasonOptions"
                :key="index"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 入库原因 end -->

          <!-- 入库人员 start -->
          <el-form-item
            label="入库人员"
            prop="operatorUserId"
            v-if="validateForm.type == '2'"
          >
            <el-select
              style="width: 100%;"
              v-model="validateForm.operatorUserId"
              filterable
              placeholder="请选择入库人员"
              clearable
            >
              <el-option
                v-for="(item, index) in putInStorageOptions"
                :key="index"
                :label="item.userName"
                :value="item.userId"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 入库人员 end -->

          <!-- 出库数量 start -->
          <el-form-item
            label="出库数量"
            prop="number"
            v-if="validateForm.type == '1'"
          >
            <el-input
              type="text"
              v-model="validateForm.number"
              placeholder="出库数量"
            ></el-input>
          </el-form-item>
          <!-- 出库数量 end -->

          <!-- 出库原因 start -->
          <el-form-item
            label="出库原因"
            prop="reason"
            v-if="validateForm.type == '1'"
          >
            <el-select
              style="width: 100%;"
              v-model="validateForm.reason"
              filterable
              placeholder="请选择出库原因"
              clearable
            >
              <el-option
                v-for="(item, index) in searchOutStockReasonOptions"
                :key="index"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 出库原因 end -->

          <!-- 领用人员 start -->
          <el-form-item
            label="领用人员"
            prop="operatorUserId"
            v-if="validateForm.type == '1'"
          >
            <el-select
              style="width: 100%;"
              v-model="validateForm.operatorUserId"
              filterable
              placeholder="请选择领用人员"
              clearable
            >
              <el-option
                v-for="(item, index) in receiveUserOptions"
                :key="index"
                :label="item.userName"
                :value="item.userId"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 领用人员 end -->

          <!-- 请输入设备编号 start -->
          <el-form-item label="设备编号" prop="equipmentNo">
            <el-input
              v-model="validateForm.equipmentNo"
              placeholder="请输入设备编号"
            >
            </el-input>
          </el-form-item>
          <!-- 请输入设备编号 end -->

          <!-- 请输入库存余量 start -->
          <el-form-item label="库存余量" prop="inventoryBalance">
            <el-input
              v-model="validateForm.inventoryBalance"
              placeholder="请输入库存余量"
            >
            </el-input>
          </el-form-item>
          <!-- 请输入库存余量 end -->

          <!-- 请输入过质保 start -->
          <el-form-item label="过质保" prop="overWarranty">
            <!-- <el-input
              v-model="validateForm.overWarranty"
              placeholder="请输入过质保"
            >
            </el-input> -->
            <el-select
              style="width: 100%;"
              v-model="validateForm.overWarranty"
              filterable
              placeholder="请选择过质保"
              clearable
            >
              <el-option
                v-for="(item, index) in overWarrantyOptions"
                :key="index"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 请输入过质保 end -->

          <!-- 请输入库存阈值 start -->
          <el-form-item label="库存阈值 " prop="threshold">
            <el-input
              v-model="validateForm.threshold"
              placeholder="请输入库存阈值"
            >
            </el-input>
          </el-form-item>
          <!-- 请输入库存阈值  end -->

          <!-- 请输入备注 start -->
          <el-form-item label="备注 " prop="remarks">
            <el-input v-model="validateForm.remarks" placeholder="请输入备注">
            </el-input>
          </el-form-item>
          <!-- 请输入备注 end -->
        </el-form>
        <!-- form表单 -->
        <!-- 提交 start -->
        <div class="submit">
          <el-button type="primary" @click="submitForm('validateForm')"
            >提交</el-button
          >
          <el-button @click="dialogClose">取消</el-button>
        </div>
        <!-- 提交 end -->
      </el-dialog>
      <!-- 提交 dialog对话框 end -->
    </el-container>
  </div>
</template>

<script>
// import JsonExcel from 'vue-json-excel'
import FileSaver from 'file-saver'
import XLSX from 'xlsx'

import { createUrl } from '../utils/utils.js'
export default {
  name: 'SettlementFlow',
  data() {
    /**
     * 公共验证函数(验证数字值)
     * @params message - 提示信息
     */
    function checkNumberMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          // 如果值为空，可以回调
          callback()
        }

        if (isNaN(Number(value))) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填数字值)
     * @params message - 提示信息
     */
    function checkRequireNumberMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        }

        if (isNaN(Number(value))) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填值)
     * @params message - 提示信息
     */
    function checkRequireMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        } else {
          callback()
        }
      }
    }

    return {
      showTable: true, // 是否显示表格
      feeInput: '', // 请输入手续费
      taxInput: '', // 请输入税点tableFields
      tableFields: {
        //导出Excel表格的表头设置
        景区名称: 'spotName',
        申请单号: 'applyNumber',
        配件编号: 'partsManagementId',
        配件名称: 'partsManagementName',
        规格型号: 'accessoryModel',
        类型: 'type',
        // 入库数量: 'number',
        // 入库原因: 'reason',
        // 入库人员: 'operatorUserId',
        出库数量: 'number',
        出库原因: 'reason',
        领用人员: 'operatorUserId',
        设备编号: 'equipmentNo',
        库存余量: 'inventoryBalance',
        更新时间: 'updateTime',
        过质保: 'overWarranty',
        库存阈值: 'threshold',
        备注: 'remarks'
        // 确认标识: 'T18',
        // 撤销标识: 'T19'
        // 操作: 'operate'
      },
      tableData: [], // 表格数据
      tableLoading: false,

      dialogTitle: '库存', // dialog的标题

      searchCompanyOptions: [], // 搜索的公司选项
      searchScenicOptions: [], // 搜索的景区选项
      receivingOptions: [], // 收货景区列表

      searchInStockReasonOptions: [
        {
          value: '1',
          label: '备件申请'
        },
        {
          value: '2',
          label: '故障上报'
        },
        {
          value: '3',
          label: '盘点'
        },
        {
          value: '4',
          label: '工具'
        },
        {
          value: '5',
          label: '仪器'
        }
      ], // 入库原因列表
      searchOutStockReasonOptions: [
        {
          value: '1',
          label: '故障维修'
        },
        {
          value: '2',
          label: '自修'
        },
        {
          value: '3',
          label: '盘点'
        }
      ], // 入库原因列表

      searchTypeOptions: [
        {
          value: '1',
          label: '出库'
        },
        {
          value: '2',
          label: '入库'
        }
      ], // 搜索时的类型列表

      typeOptions: [
        {
          value: '1',
          label: '出库'
        },
        {
          value: '2',
          label: '入库'
        }
      ], // 类型列表

      overWarrantyOptions: [
        {
          value: "0",
          label: '否'
        },
        {
          value: "1",
          label: '是'
        },
      ],

      searchSpotNameOptions: [], // 搜索时供货商选项
      searchGoodsNameOptions: [], // 搜索时配件名称选项
      searchReceivingNameOptions: [], // 搜索时收货单位选项

      goodsNameOptions: [], // 提交时配件名称选项

      receiveUserOptions: [], // 提交时的领用人员列表（根据景区联动）
      putInStorageOptions: [], // 提交时的领用人员列表（根据景区联动）
      scenicOptions: [], // 提交时的景区选项

      // 快捷选项配置
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now()
        },
        shortcuts: [
          {
            text: '今天',
            onClick(picker) {
              picker.$emit('pick', new Date())
            }
          },
          {
            text: '昨天',
            onClick(picker) {
              const date = new Date()
              date.setTime(date.getTime() - 3600 * 1000 * 24)
              picker.$emit('pick', date)
            }
          },
          {
            text: '一周前',
            onClick(picker) {
              const date = new Date()
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7)
              picker.$emit('pick', date)
            }
          }
        ]
      },

      exportTableData: [], // 导出的全部表格数据
      downloadLoading: false, // 导出excel按钮的加载动画
      importLoading: false, // 导入excel按钮的加载动画

      currentPage: 1, // 当前页数,
      pageSize: 10, // 每页显示条数

      selectDateState: '1', // 日期统计方式（月，日，年）
      dateType: 'daterange', // 日期类型（月，日，年）
      valueFormat: 'yyyy-MM-dd', // 日期格式

      searchValidateForm: {}, // 搜索表单验证对象
      validateForm: {}, // 表单验证对象
      editValidateForm: {}, // 表单验证对象
      previewValidateForm: {
        previewOrderNumber: '' // 预览单号
      }, // 预览表单对象

      searchPartnerCompanyName: '', // 搜索的公司
      searchScenicName: '', // 搜索的景区
      searchSelectDateState: '1', // 搜索的日期统计方式
      searchStartEndDate: '', // 搜索的起止时间
      searchPayState: '', // 搜索的支付状态

      companys: [], // 公司列表
      companyState: '', // 当前请选择公司

      scenics: [], // 景区列表
      scenicState: '', // 当前请选择景区

      downloadUrl: '',

      total: 0,

      operateStatus: 0, // 操作状态（0、初始状态；1、确认；2、撤销）

      dialogVisible: false,

      dialogState: '', // 当前dialog弹窗是新增还是编辑
      currentRowOrderNumber: '', // 当前行单号

      // previewOrderNumber: "", // 预览单号

      // 搜索表单规则
      searchValidateFormRules: {},
      // 预览表单规则
      previewValidateFormRules: {
        // 单号
        previewOrderNumber: [
          {
            validator: checkRequireMethod('单号'),
            required: true,
            trigger: ['blur', 'change']
          }
        ]
      },

      // 提交表单规则
      validateFormRules: {
        // 景区名称
        spotName: [
          {
            validator: checkRequireMethod('景区名称'),
            trigger: ['blur', 'change']
          }
        ],

        // 申请单号
        applyNumber: [
          {
            validator: checkRequireMethod('申请单号'),
            trigger: ['blur', 'change']
          }
        ],

        // 配件编号
        partsManagementId: [
          {
            validator: checkRequireMethod('配件编号'),
            trigger: ['blur', 'change']
          }
        ],

        // 配件名称
        partsManagementName: [
          {
            validator: checkRequireMethod('配件名称'),
