  methods: {
    // 获取搜索表单验证对象
    getSearchValidateForm() {
      var validateForm = {}
      for (var item in this.tableFields) {
        validateForm[this.tableFields[item]] = null
      }
      this.searchValidateForm = {
        ...validateForm,
        goodId: null,
        spotId: null,
        type: '1'
      }
    },

    // 获取提交表单验证对象
    getValidateForm() {
      var validateForm = {}
      for (var item in this.tableFields) {
        validateForm[this.tableFields[item]] = null
      }
      this.validateForm = {
        ...validateForm,
        goodId: null,
        spotId: null,
        type: '1'
      }
      // console.log(this.validateForm);
    },

    // 登录(必须)
    async login() {
      const { data: res } = await this.$http.post(
        '/loginSystem?username=lijiazhao&password=lijiazhao'
      )
    },

    /**
     * 领用或者入库人员下拉选
     */
    async operatorUserDrop() {
      this.tableLoading = true

      const { data: res } = await this.$http.get(
        '/system/operatePartsStock/operatorUserDrop',
        {
          params: {}
        }
      )
      // console.log(res);
      if (res.state == '200') {
        this.putInStorageOptions = res.data
        this.receiveUserOptions = res.data

        this.tableLoading = false
      }
    },

    /**
     * 初始化表格数据
     */
    async initTableData() {
      var startDate = this.searchStartEndDate && this.searchStartEndDate[0]
      var endDate = this.searchStartEndDate && this.searchStartEndDate[1]

      this.tableLoading = true

      const { data: res } = await this.$http.get(
        '/system/operatePartsStock/list',
        {
          params: {
            ...this.searchValidateForm,
            startDate,
            endDate,
            // pageNum: (this.currentPage - 1) * this.pageSize, // 页码
            pageNum: this.currentPage, // 页码
            pageSize: this.pageSize // 每页条数
          }
        }
      )
      // console.log(res);
      if (res.code == '200') {
        this.tableData = res.data
        this.total = res.totals || 0

        this.tableLoading = false
      }
    },

    /**
     * 新增表格数据
     */
    async addTableData() {
      const { data: res } = await this.$http.post(
        '/system/operatePartsStock/addOperatePartsStock',
        this.validateForm
      )
      if (res.state == '200') {
        this.$message({
          message: res.msg,
          type: 'success',
          offset: 100,
          center: true,
          duration: 1000
        })
        this.initTableData()
        this.$refs.validateForm.resetFields() // 重置表单
      } else {
        this.$message({
          message: res.msg,
          type: 'error',
          offset: 100,
          center: true,
          duration: 3000
        })
      }
    },

