    // 导入格式
    importfxx(obj) {
      let _this = this
      // 通过DOM取文件数据
      this.file = obj
      var rABS = false //是否将文件读取为二进制字符串
      var f = this.file
      var reader = new FileReader()
      //if (!FileReader.prototype.readAsBinaryString) {
      FileReader.prototype.readAsBinaryString = function(f) {
        var binary = ''
        var rABS = false //是否将文件读取为二进制字符串
        var pt = this
        var wb //读取完成的数据
        var outdata
        var reader = new FileReader()
        reader.onload = function(e) {
          var bytes = new Uint8Array(reader.result)
          var length = bytes.byteLength
          for (var i = 0; i < length; i++) {
            binary += String.fromCharCode(bytes[i])
          }
          var XLSX = require('xlsx')
          if (rABS) {
            wb = XLSX.read(btoa(fixdata(binary)), {
              //手动转化
              type: 'base64'
            })
          } else {
            wb = XLSX.read(binary, {
              type: 'binary'
            })
          }
          outdata = XLSX.utils.sheet_to_json(wb.Sheets[wb.SheetNames[0]]) //outdata就是你想要的东西
          console.log(outdata)
          this.da = [...outdata]
          let arr = []
          this.da.map((v) => {
            let obj = {
              robotCount: v['机器人数量'],
              type: v['发货进度'],
              applicantName: v['申请人'],
              factoryName: v['发货工厂'],
              consignorName: v['发货人'],
              receivingName: v['收货景区'],
              consigneeName: v['收货人'],
              consigneePhone: v['收货人电话'],
              receiningAddress: v['收货地址'],
              consignmentDate: v['发货时间']
            }
            arr.push(obj)
          })
          // 将 arr 赋值给 tableData，来更新表格数据
          this.tableData = arr
          return arr
        }
        reader.readAsArrayBuffer(f)
      }

      if (rABS) {
        reader.readAsArrayBuffer(f)
      } else {
        reader.readAsBinaryString(f)
      }
    },

    // 提交表单配件名称变化时触发
    onChangeGoods(name) {
      this.goodsNameOptions.forEach((item) => {
        if (item.name == name) {
          this.validateForm.partsManagementId = item.id
          this.validateForm.accessoryModel = item.model
        }
      })
    },

    // 导入EXCEL文件超出限制时触发
    handleImportExceed(files) {
      this.$refs.uploadImport.clearFiles()
      // const file = files[0]
      this.$refs.uploadImport.submit(files)
    },

    // 导入EXCEL文件状态改变时触发
    handleImportChange(uploadFile, uploadFiles) {
      console.log(uploadFile)
      this.importE(uploadFile.raw)
    },

    // 搜索表单，类型切换时触发
    onSearchChangeType(value) {
      
    }
  },
  components: {
    // DownloadExcel: JsonExcel,
  },
  computed: {
    tableContentValue() {
      return (row, value) => {
        // console.log(row, value)
        var result = ''
        if (value == 'type') {
          if (row[value] == 1) {
            result = '待发货'
          } else if (row[value] == 2) {
            result = '已发货'
          } else if (row[value] == 3) {
            result = '已收货'
          } else if (row[value] == 4) {
            result = '已关闭'
          }
        } else if(value == 'overWarranty') {
          if(row[value] == "0") {
            result = '否'
          } else if(row[value] == "1") {
            result = '是'
          }
        } else {
          result = row[value]
        }
        return result
      }
    }
  }
}
</script>

